@use './vars.scss';

@keyframes draw {
  0% {
    stroke-dashoffset: 1100;
    fill-opacity: 0;
    
  }
  80%{
    stroke-dashoffset: 0;
    fill-opacity: 0;
  }
  100% {
    stroke-dashoffset: 0;
    fill-opacity: 1;
  }
}

@keyframes line-dasharray {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes filling {
  0% {
    fill: transparent;
  }

  100% {
    fill: vars.$primary-color-04;
  }
}

@keyframes filling-bis {
  0% {
    fill: transparent;
  }

  100% {
    fill: vars.$primary-color-03;
  }
}